@import "normalize.scss";
$gray:#929ba0;
$black:#3f4244;
$accent-green:linear-gradient(to top, #4cdfcc, #50e6cc, #56eccb, #5ef3c9, #67f9c7);
$accent-red:linear-gradient(to top, #ff3858, #ff4856, #ff5555, #ff6154, #ff6c54);
@mixin tite-subtit {
    text-align: center;
   
}
@mixin anim {
    transition: 0.3s ease-in-out;
}
@mixin redbutton {
    text-decoration: none;
            font-size: 15px;
            padding: 26px 55px;
            border-radius: 10px;
            color: #fff;
            background-image: $accent-red;
            @include anim();
}
@mixin content {
        text-align: center;
        color: #fff;
       
        h2{
            padding-bottom: 33px;
        }
       p{

       color: #fff;
       max-width: 770px;
       margin: 0 auto;
       }
    
}



.container{
    margin: 0 auto;
    max-width: 1170px;
   padding-left: 10px;
   padding-right: 10px;
}

body{
    font-family: 'Roboto', sans-serif;
}

h1{
    font-size: 60px;
    font-weight: 300;
    color:#fff;
}

h3{
    font-weight: 500;
    font-size: 15px;
}

h2{
    font-size: 40px;
    font-weight: 400;
    text-transform: capitalize;
}
p{
    font-size: 15px;
    color: $gray;
    font-weight: 300;
}

h1,h2,h3,h4{
    margin: 0;
    padding: 0;
}

.page-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
    @media (max-width:880px) {
        flex-direction: column;
    }
    &__logo{
        img{
            width: 100px;
            height: 43px;
        }
    }
    &__item{
        display: flex;
        justify-content: flex-end;
        @media (max-width:880px) {
            flex-direction: column;
            
        }
        li{
           list-style-type: none;
           margin-left: 20px;
           @media (max-width:880px) {
            margin-left: 0;
        }
           a{
            text-decoration: none;
            display: block;
            padding: 15px 29px;
            border-radius: 13px;
            letter-spacing: 0.2px;
            color: $black;
            font-size: 15px;
            
               @include anim();
           }
           a:hover{
            background-image: linear-gradient(to top, #4cdfcc, #50e6cc, #56eccb, #5ef3c9, #67f9c7);
            color: #fff;
            @include anim();
           }
        }
    }
}
.gradient{
    background-image: $accent-green;
}

.page-try{
    padding-top: 259px;
    padding-bottom: 276px;
    &__content{
        text-align: center;
        h1{
            padding-bottom: 79px;
            @media (max-width:880px) {
                font-size: 35px;
            }
        }
        a{
            text-decoration: none;
            font-size: 15px;
            padding: 26px 55px;
            border-radius: 10px;
            color: #fff;
            background-image: $accent-red;
            @include anim();
            @media (max-width:880px) {
                padding: 15px 35px;
            }
        }
        
    }
}

.page-services{
    padding-top: 82px;
    padding-bottom: 105px;
    @media (max-width:1208px) {
        padding-top: 50px;
    }
    &__content{
        text-align: center;
        &__block{
        display: grid;
        grid-template-columns: 236px 269px 270px 239px;
        grid-template-rows: 262px;
        justify-content: center;
        grid-column-gap: 50px;
       padding-bottom: 127px;
       @media (max-width:1208px) {
        grid-template-columns: 236px 269px;
        grid-template-rows: 262px 262;
        grid-row-gap: 60px;
        padding-bottom: 50px;
    }
    @media (max-width:608px) {
        grid-template-columns: 236px ;
        grid-template-rows: 262px 262px 262px;
        grid-row-gap: 60px;
        padding-bottom: 50px;
    }
        &__item{
            img{
                padding-bottom: 28px;
            }
            h3{
                padding-bottom: 25px;
            }
        }
    }

    a{
        background-image: $accent-red;
        font-size: 20px;
        padding: 23px 48px;
        text-decoration: none;
        color: #fff;
        text-align: center;
        margin: 0 auto;
        border-radius: 10px;
        @media (max-width:1208px) {
            padding: 16px 36px;
        }
    }
}
}

.red-gradient{
    background-image: $accent-red;
}

