.page-respons{
    padding-top: 66px;
    padding-bottom: 69px;
    &__block{

    
    display: flex;
    justify-content: space-around;
    @media (max-width:1208px) {
        flex-direction: column;
        text-align: center;
    }
    
    &__item{
        flex-basis: 47%;
        
        img{
            margin-left: 142px;
