}


.page-design{
    padding-top: 82px;
    padding-bottom: 71px;
   i{
    display: flex;
    justify-content: center;
    font-size: 41px;
   
    color: #fff;
   }
    &__content{
        @include content() ; 
        padding-bottom: 41px;
        
    &__buttons{
        margin-top: 107px;
        a{
            color: $black;
            font-size: 15px;
            text-decoration: none;
            margin-right: 41px;
            @media (max-width:420px) {
                font-size: 13px;
            }
        }
        a:hover{
            color: #fff;
            @include anim();
        }
    }
    }
    &__block{
        display: grid;
        grid-template-columns: 370px 370px 370px;
        grid-template-rows: 370px 370px;
