            grid-template-rows: 370px 370px 370px;
        }
        @media (max-width:888px) {
            justify-content: center;
            grid-template-columns: 370px ;
            grid-template-rows: 370px 370px 370px 370px;
        }
        @media (max-width:420px) {
            justify-content: center;
            grid-template-columns: 250px  ;
            grid-template-rows: 370px 370px 370px;
        }
        &__item{
            text-align: center;
            background: #fff;
            img{
                
                height: 240px;
                width: 370px;
                padding-bottom: 20px;
                @media (max-width:420px) {
                   width: 250px;
