.page-contact{
    padding-top: 78px;
    padding-bottom: 174px;
    @media (max-width:1208px) {
       padding-bottom: 50px;
    }
    &__content{
        @include content();
        padding-bottom: 76px;
        @media (max-width:880px) {
          padding-bottom: 20px;
        }
    }
    &__form{
        text-align: center;
        padding-top: 24px;

        position: relative;
        
        input{
            display: inline-block;
   
    box-sizing: border-box;
    padding: 17px 40px;
    outline: none;
    border: 1px solid #fff;
    background: transparent;
    color: #fff;
    width: 100%;
    max-width: 372px;
    margin-right: 12px;
    @media (max-width:880px) {
        margin-top: 20px;
    }
        }
        &__text{
            box-sizing: border-box;
            padding: 17px 26px;
            outline: none;
            border: 1px solid #fff;
            background: transparent;
            color: #fff;
            width: 100%;
            max-width: 768px;
            resize: none;
            margin-top: 84px;     
            @media (max-width:880px) {
               max-width: 568px;
            }
         }
         button{
            text-align: center;
            position: absolute;
            right: 197px;
            bottom: 88px;
            padding: 17px 36px;
            background: #3f4244;
            color: #fff;
            font-size: 15px;
            border: 1px solid #3f4244;
            outline: none;
            cursor: pointer;
